* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: sans-serif;
}

body {
  height: 100%;
  background: #1f5a82;
}

.container {
  background: #fff;
  width: 400px;
  padding: 35px;
  margin: 50px auto;
  border-radius: 10px;
  box-shadow: 15px 30px 35px rgba(0, 0, 0, 0.1);
}

.coin {
  height: 200px;
  width: 200px;
  position: relative;
  margin: 32px auto;
  transform-style: preserve-3d;
}

.coin img {
  width: 100%;
}

.heads,
.tails {
  position: absolute;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
  z-index: 0;
}

.heads {
  transform: rotateY(180deg);
}

@keyframes spin-tails {
  0% {
    transform: rotateY(0);
  }

  100% {
    transform: rotateY(1980deg);
  }
}

@keyframes spin-heads {
  0% {
    transform: rotateY(0);
  }

  100% {
    transform: rotateY(2160deg);
  }
}

.stats {
  display: flex;
  justify-content: space-between;
  color: #101020;
  font-weight: 500;
  padding: 20px;
  margin-top: 55px;
  margin-bottom: 40px;
  box-shadow: 0 0 20px rgba(0, 139, 253, 0.25);
}

.buttons {
  display: flex;
  justify-content: space-between;
}

button {
  width: 150px;
  padding: 15px 0;
  border: none;
  font-size: 16px;
  border-radius: 5px;
  cursor: pointer;
}

#flip-button {
  background: #053469;
  color: white;
  transition: background 400ms, border 400ms, color 400ms;
}

#flip-button:disabled {
  background: #e1e0ee;
  border-color: #e1e0ee;
  color: #101020;
}

#reset-button {
  background: #674706;
  color: white;
}